.moviesList-itemList {
  margin-bottom: 30px;
  width: 30%; }

.moviesList-itemListLink {
  display: inline-block;
  height: 435px;
  width: 100%; }

.moviesList-itemListImage {
  width: 100%; }

.moviesList-itemListTitle {
  align-items: flex-start;
  display: flex;
  justify-content: space-between;
  padding: 5px; }

.moviesList-itemListName {
  text-transform: uppercase;
  color: #525462;
  font-weight: bold;
  font-size: 14px; }

.moviesList-itemListDate {
  color: #525462;
  border-radius: 2px;
  border: 1px solid #ddd;
  font-weight: bold;
  padding: 1px 7px;
  font-size: 13px; }

.moviesList-itemListDescription {
  color: #868996;
  padding: 0 5px; }
