.btn {
  text-transform: uppercase;
  border: none;
  border-radius: 2px;
  min-width: 80px; }
  .btn:hover {
    cursor: pointer; }
  .btn--primary {
    color: #d05a6d;
    background-color: #fff; }
  .btn--secondary {
    background-color: #d05a6d;
    color: #fff; }
  .btn--third {
    background-color: rgba(125, 125, 125, 0.5);
    color: #fff; }
  .btn--fourth {
    background-color: #fff;
    color: #d05a6d; }
  .btn--transparent {
    background: none; }
  .btn--small {
    padding: 4px 12px;
    font-size: 12px; }
  .btn--medium {
    padding: 8px 15px;
    font-size: 13px; }
